/* theme */
.lbtn.lbtn-wordpress {
    color: #ffffff;
    background-color: #494541;
    border-color: #564432;
}

.lbtn.lbtn-wordpress.white {
    color: #494541;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-wordpress .logo::before {
    content: "\E00F";
}

.lbtn.lbtn-wordpress.white .logo {
    color: #494541;
}