/* theme */
.lbtn.lbtn-twitter {
    color: #ffffff;
    background-color: #1da1f2;
    border-color: #349ad9;
}
.lbtn.lbtn-twitter.white {
    color: #1da1f2;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-twitter .logo::before {
    content: "\E00E";
}

.lbtn.lbtn-twitter.white .logo {
    color: #1da1f2;
}