/* theme */
.lbtn.lbtn-instagram {
    color: #ffffff;
    background-color: #e53c5c;
    border-color: #ce536a;
}

.lbtn.lbtn-instagram.white {
    color: #e53c5c;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-instagram .logo::before {
    content: "\E005";
}

.lbtn.lbtn.lbtn-instagram.white .logo {
    color: #e53c5c;
}